.registerContainer {
  display: flex;
  justify-content: center;
  height: 950px;
  background-color: gray;
}
.containerRegister {
  width: 400px;
  height: 340px;
  margin: auto;
  margin-top: 180px;
  padding: 20px 30px;
  border-top: 4px solid #017bab;
  box-shadow: 7px 13px 37px #000;
  background-color: #4e4d4d;
  color: white;
}
h4 {
  height: 40px;
  margin: 0;
  margin-bottom: 30px;
  text-align: center;
  border-bottom: 1px solid;
  font-size: 20px;
}
.registerEmail,
.registerPassword {
  width: 95%;
  padding: 11px 10px;
  margin-bottom: 15px;
  border-radius: 12px;
  border: 1px solid #017bab;
  background: #252323;
  font-size: 14px;
  font-weight: bold;
  color: #fff;
}
.btnSubmit {
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  border-radius: 12px;
  border: none;
  background: #017bab;
  color: white;
}
.btnBackHome {
  display: flex;
  justify-content: center;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
.btnBackHome,
.btnSubmit {
  color: white;
  font-size: 14px;
  text-decoration: none;
  cursor: pointer;
}
.btnBackHome:hover {
  text-decoration: underline;
}
@media (min-width: 320px) {
  .containerRegister {
    width: 240px;
    height: 420px;
    text-align: center;
  }
}
@media (min-width: 414px) {
  .containerRegister {
    width: 300px;
    height: 420px;
    margin-top: 100px;
  }
}
@media (min-width: 768px) {
  .containerRegister {
    width: 350px;
    height: 420px;
  }
}
@media (min-width: 900px) {
  .containerRegister {
    width: 400px;
    height: 400px;
  }
}
