.containerMenu {
  display: flex;
  flex-direction: column;
  height: 950px;
  top: 0px;
}
* {
  font-family: sans-serif; /* Change your font family */
}
.overlayDisabled {
  position: fixed;
  display: flex;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
  visibility: hidden;
}
.overlayEnabled {
  position: fixed;
  display: flex;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
  visibility: visible;
}
.popUpEnabled {
  position: absolute;
  width: 350px;
  background-color: transparent;
  visibility: visible;
}
.popUpDisabled {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 0px;
  height: 0px;
  visibility: hidden;
}
.containerAddStudent {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 400px;
  height: 400px;
  top: 10%;
  left: 35%;
  padding: 20px 30px;
  border-top: 4px solid #017bab;
  background-color: #4e4d4d;
  box-shadow: 7px 13px 37px #000;
  color: white;
}
h4 {
  height: 40px;
  margin: 0;
  margin-bottom: 30px;
  text-align: center;
  border-bottom: 1px solid;
  font-size: 20px;
}
.nameStudent,
.lastnameStudent,
.ageStudent,
.classStudent {
  width: 95%;
  padding: 11px 10px;
  margin-bottom: 15px;
  border: 1px solid #017bab;
  border-radius: 12px;
  background: #252323;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}
.btnAddStudent {
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  border-radius: 12px;
  background: #017bab;
  border: none;
  color: white;
}
.overlayDisabledMark {
  position: fixed;
  display: flex;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
  visibility: hidden;
}
.overlayEnabledMark {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
  visibility: visible;
}
.popUpDisabledMark {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 0px;
  height: 0px;
  visibility: hidden;
}
.popUpEnabledMark {
  position: absolute;
  width: 350px;
  visibility: visible;
  background-color: transparent;
}
.containerAddMarks {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 400px;
  height: 400px;
  top: 10%;
  left: 35%;
  padding: 20px 30px;
  border-top: 4px solid #017bab;
  background-color: #4e4d4d;
  box-shadow: 7px 13px 37px #000;
  color: white;
}
.subjectMark,
.noteMark {
  width: 95%;
  padding: 11px 10px;
  margin-bottom: 15px;
  border: 1px solid #017bab;
  border-radius: 12px;
  background: #252323;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}
.nameStudentMark {
  width: 100%;
  padding: 11px 10px;
  margin-bottom: 15px;
  border: 1px solid #017bab;
  border-radius: 12px;
  background: #252323;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}
.btnAddMark {
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  border-radius: 12px;
  background: #017bab;
  color: white;
  border: none;
}
.content-table-student,
.content-table-mark {
  margin: 25px 0;
  min-width: 400px;
  border-radius: 5px 5px 0 0;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  border-collapse: collapse;
  font-size: 0.9em;
  overflow: hidden;
}
.content-table-student thead tr,
.content-table-mark thead tr {
  text-align: left;
  background-color: #017bab;
  color: #ffffff;
  font-weight: bold;
}
.content-table-student th,
.content-table-student td,
.content-table-mark th,
.content-table-mark td {
  padding: 12px 15px;
}
.content-table-student tbody tr,
.content-table-mark tbody tr {
  border-bottom: 1px solid #dddddd;
}
.content-table-student tbody tr:nth-of-type(even),
.content-table-mark tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}
.content-table-student tbody tr:last-of-type,
.content-table-mark tbody tr:last-of-type {
  border-bottom: 2px solid #017bab;
}
.content-table-student tbody tr.active-row-student,
.content-table-mark tbody tr.active-row-mark {
  font-weight: bold;
  color: #009879;
}
.btnAdd {
  height: 25px;
  width: 70px;
  margin: 10px;
  border-radius: 15px;
  box-shadow: 7px 13px 37px #000;
  background-color: #2175bc;
  color: #fff;
  border: none;
  font-size: 15px;
  font-family: Arial;
  text-decoration: none;
}
.btnDeleteStudent {
  border-radius: 3px;
  color: white;
  border: none;
  background-color: #2175bc;
  cursor: pointer;
}
.btnDeleteQualification {
  margin-left: 15px;
  margin-top: 8px;
  left: 4%;
  border-radius: 3px;
  color: white;
  border: none;
  background-color: #2175bc;
  cursor: pointer;
}
.btnLogOut {
  margin: 10px;
  padding: 5px 20px 5px 20px;
  border-radius: 15px;
  box-shadow: 7px 13px 37px #000;
  background-color: #9c2819;
  font-family: Arial;
  font-size: 15px;
  color: #fff;
  text-decoration: none;
}
