.containerHome {
  display: flex;
  justify-content: center;
  padding-top: 180px;
  align-items: center;
  transition: padding-top 0.5s ease;
  background-color: #ecf0f1;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.header {
  position: fixed;
  width: 100%;
  height: 235px;
  top: 0;
  text-align: center;
  box-shadow: 7px 1px 37px #000;
  background-color: #e73c3c;
  overflow: hidden;
  transition: all 0.5s ease;
}
.text1 {
  position: relative;
  margin-bottom: 20px;
  background: #e73c3c;
  color: white;
  font-size: 60px;
  font-weight: 700;
  letter-spacing: 8px;
  animation: text 3s 1;
}
.text2 {
  color: black;
  font-size: 30px;
  font-family: "Times New Roman", Times, serif;
}
.nav {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 60px;
  bottom: 0;
  width: 100%;
  line-height: 60px;
  background: rgba(182, 59, 59, 0.8);
}
.login,
.register {
  color: white;
  text-decoration: none;
}
ul {
  display: flex;
  justify-content: center;
  width: 70%;
  margin: auto;
  list-style: none;
}
li {
  padding: 10px 15px;
  margin-top: 13px;
  border-radius: 5px;
  line-height: 1;
  font-size: 15px;
}
div div div li:hover {
  background: rgba(0, 0, 0, 0.2);
}
h2 {
  font-size: 34px;
  font-weight: 400;
  line-height: 2;
  color: #2c3e50;
  text-transform: uppercase;
}
p {
  margin-bottom: 2rem;
  line-height: 2;
  color: black;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
.wrapper {
  max-width: 800px;
  margin: 0 auto;
}
.section {
  margin: 10px;
  padding: 20px;
  border-radius: 4px;
  background: rgb(255, 255, 255);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
}
@keyframes text {
  0% {
    margin-bottom: -90px;
    color: black;
  }

  30% {
    margin-bottom: -10px;
    letter-spacing: 25px;
  }

  85% {
    margin-bottom: -0px;
    letter-spacing: 8px;
  }
}
@media (min-width: 320px) {
  .text1 {
    font-size: 35px;
    margin-top: 45px;
  }
  .text1 {
    animation: none;
  }
}
@media (min-width: 414px) {
  .text1 {
    font-size: 40px;
  }
  .text1 {
    animation: none;
  }
}
@media (min-width: 768px) {
  .text1 {
    font-size: 45px;
  }
  .text1 {
    animation: none;
  }
}
@media (min-width: 900px) {
  .text1 {
    font-size: 50px;
  }
  .text1 {
    animation: none;
  }
}
@media (min-width: 1024px) {
  .text1 {
    font-size: 55px;
    margin-top: 35px;
  }
  .text1 {
    animation: none;
  }
}
@media (min-width: 1200px) {
  .text1 {
    font-size: 60px;
  }
  .text1 {
    animation: none;
  }
}
